<template>
  <div class="container">
    <Header
      title="Phonebook"
      @toggle-add-entry="toggleAddEntry"
      :showAddEntry="showAddEntry"
    />
    <div v-show="showAddEntry">
      <AddEntry @add-entry="addEntry" />
    </div>
    <div v-show="showEditEntry">
      <EditEntry @edit-entry="editEntry" />
    </div>
    <EntryList
      @open-edit-entry="openEditEntry"
      @delete-entry="deleteEntry"
      :entries="entries"
    />
  </div>
</template>

<script>
import Header from "./components/Header";
import EntryList from "./components/EntryList";
import AddEntry from "./components/AddEntry";
import EditEntry from "./components/EditEntry";

export default {
  name: "App",
  components: {
    Header,
    EntryList,
    AddEntry,
    EditEntry,
  },
  data() {
    return {
      search: "",
      entries: [],
      showAddEntry: false,
      showEditEntry: false,
    };
  },
  methods: {
    addEntry(newEntry) {
      this.entries = [...this.entries, newEntry];
      this.sortEntries();
      this.updateStorage();
    },
    editEntry(entry) {
      console.log("entry", entry);
    },
    openEditEntry(entry) {
      this.showEditEntry = true;
      this.showAddEntry = false;
      this.editEntry(entry);
    },
    deleteEntry(id) {
      console.log(id);
      this.entries = this.entries.filter((entry) => entry.id !== id);
      this.updateStorage();
    },
    toggleAddEntry() {
      this.showEditEntry = false;
      this.showAddEntry = !this.showAddEntry;
    },
    sortEntries() {
      this.entries = Object.values(this.entries);
      this.entries.sort((a, b) => (a.firstName > b.firstName ? 1 : -1));
    },
    updateStorage() {
      localStorage.phoneBook = JSON.stringify(this.entries);
    },
    loadStorage() {
      this.entries = JSON.parse(localStorage.phoneBook);
    },
  },
  created() {
    if (localStorage.phoneBook) {
      this.loadStorage();
    } else {
      this.entries = [
        {
          id: 1,
          firstName: "Hello",
          lastName: "World",
          phoneNumber: "0701234567",
        },
        {
          id: 2,
          firstName: "Foo",
          lastName: "Bar",
          phoneNumber: "0712345678",
        },
        {
          id: 3,
          firstName: "Fizz",
          lastName: "Buzz",
          phoneNumber: "0723456789",
        },
      ];
    }
  },
};
</script>

