<template>
  <div :key="entry.id" v-for="entry in entries">
    <entry
      @open-edit-entry="$emit('open-edit-entry', entry)" 
      @delete-entry="$emit('delete-entry', entry.id)"
      :entry="entry"
    />
  </div>
</template>

<script>
import entry from './Entry'
export default {
  name: 'Entries',
  props: {
    entries: Array,
  },
  components: {
    entry,
  },
  emits: [
    'open-edit-entry',
    'delete-entry'
  ],
}
</script>