<template>
  <h3>Edit Entry</h3>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>First name</label>
      <input type="text" v-model="firstName" name="firstName" />
    </div>
    <div class="form-control">
      <label>Last name</label>
      <input type="text" v-model="lastName" name="lastName" />
    </div>
    <div class="form-control">
      <label>Phone Number</label>
      <input type="number" v-model="phoneNumber" name="phoneNumber" />
    </div>

    <input type="submit" value="Edit Entry" class="btn btn-block" />
  </form>
</template>

<script>
export default {
  name: "EditEntry",
  props: {
    initialEntry: Object, 
  },
  data() {
    return {
      id: "",
      firstName: this.initialFirstName,
      lastName: this.initialLastName,
      phoneNumber: this.initialPhoneNumber,
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      if (!this.firstName || !this.lastName || !this.phoneNumber) {
        alert("Make sure all fields are filled correctly");
        return;
      }

      const editEntry = {
        id: this.initialId,
        firstName: this.firstName,
        lastName: this.lastName,
        phoneNumber: this.phoneNumber,
      }
      
      console.log(editEntry);

      this.$emit("edit-entry", editEntry);
      
      this.id = "";
      this.firstName = "";
      this.lastName = "";
      this.phoneNumber = "";
    },
  },
};
</script>

<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}
.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}
.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1;
}
.form-control-check input {
  flex: 2;
  height: 20px;
}
</style>